import React, { Component } from "react";
import posed from "react-pose";
import Typed from "typed.js";

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

export default class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      linkWA:
        "https://web.whatsapp.com/send?phone=6281907456710&text=Hii%20Arfan,%20",
      pp: require("../assets/images/pp.png")
    };

    this.typeMe = this.typeStudy.bind(this);
  }

  componentDidMount() {
    if (
      navigator.userAgent.match(/Android/i) ||
      navigator.userAgent.match(/webOS/i) ||
      navigator.userAgent.match(/iPhone/i) ||
      navigator.userAgent.match(/iPad/i) ||
      navigator.userAgent.match(/iPod/i) ||
      navigator.userAgent.match(/BlackBerry/i) ||
      navigator.userAgent.match(/Windows Phone/i)
    ) {
      this.setState({
        linkWA:
          "https://api.whatsapp.com/send?phone=6281907456710&text=Hii%20Arfan,%20"
      });
    }
    this.typeStudy()
  }

  componentDidUpdate(){
    this.typed.destroy();
    this.typeStudy();
  }

  typeStudy() {
    this.typed = new Typed("#prof", {
      strings: this.props.data.lang.profession || [
        "No Profession",
        "Profession Not defined"
      ],
      typeSpeed: 50,
      backSpeed: 30,
      backDelay: 1500,
      startDelay: 1000,
      cursorChar: "_",
      loop: true
    });
  }

  render() {
    return (
      <El>
        <div id="home">
          <div
            className="header"
            style={{
              backgroundColor: "#343a40"
            }}
          >
            <div className="row">
              <div id="photo" className="col-md-6">
                <img
                  src={this.state.pp}
                  style={{
                    width: 270,
                    height: 270
                  }}
                  alt="Profile"
                />
              </div>
              <div id="id1" className="col-md-6">
                <h1 id="name">{this.props.data.name}</h1>
                <p id="prof" title="Education" />
                <div id="links">
                  <a href={this.props.data.links.github}>
                    <span className="fa fa-github" />
                  </a>
                  <a href={this.props.data.links.facebook}>
                    <span className="fa fa-facebook-square" />
                  </a>
                  <a href={this.props.data.links.wordpress}>
                    <span className="fa fa-wordpress" />
                  </a>
                  <a href={this.props.data.links.linkedin}>
                    <span className="fa fa-linkedin-square" />
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div className="content">
            <div className="row">
              <div id="about" className="col-lg-6">
                <h3>{this.props.data.lang.subtitle1}</h3>
                <p>{this.props.data.lang.desc1}</p>
                <a
                  href={this.props.data.lang.linksWA}
                  className="btn btn-success mb-3"
                >
                  <span className="fa fa-whatsapp" />
                  &nbsp; {this.props.data.lang.labelWA}
                </a>
              </div>
              <div id="id2" className="col-lg-6">
                <h3>{this.props.data.lang.subtitle2}</h3>
                <ul>
                  <li>
                    <span className="label">
                      {this.props.data.lang.desc2.label[0]}
                    </span>
                    <span className="value">
                      {this.props.data.lang.desc2.value[0]}
                    </span>
                  </li>
                  <li>
                    <span className="label">
                      {this.props.data.lang.desc2.label[1]}
                    </span>
                    <span className="value">
                      {this.props.data.lang.desc2.value[1]}
                    </span>
                  </li>
                  <li>
                    <span className="label">
                      {this.props.data.lang.desc2.label[2]}
                    </span>
                    <span className="value">
                      {this.props.data.lang.desc2.value[2]}
                    </span>
                  </li>
                  <li>
                    <span className="label">
                      {this.props.data.lang.desc2.label[3]}
                    </span>
                    <span className="value">
                      {this.props.data.lang.desc2.value[3]}
                    </span>
                  </li>
                  <li>
                    <span className="label">
                      {this.props.data.lang.desc2.label[4]}
                    </span>
                    <span className="value">
                      {this.props.data.lang.desc2.value[4]}
                    </span>
                  </li>
                  <li>
                    <span className="label">
                      {this.props.data.lang.desc2.label[5]}
                    </span>
                    <span className="value">
                      {this.props.data.lang.desc2.value[5]}
                    </span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </El>
    );
  }
}
