import React, { Component } from "react";
import posed from "react-pose";

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

const Container = posed.div({
  enter: {
    staggerChildren: 50
  }
});

export default class Services extends Component {
  render() {
    const ItemEdu = this.props.data.lang.services.name.map((el, i) => (
      <div className="col-md-3 col-sm-6" key={i}>
        <div className={"service-block id" + i}>
          <div className="service-info">
            <div className="service-image">
              <span
                className={"fa " + this.props.data.lang.services.faIcon[i]}
              />
            </div>
          </div>
          <h5>{this.props.data.lang.services.name[i]}</h5>
          <p>{this.props.data.lang.services.desc[i]}</p>
        </div>
      </div>
    ));
    return (
      <Container>
        <div id="services">
          <El>
            <div
              className="header"
              style={{
                backgroundColor: "#343a40"
              }}
            >
              <h1>{this.props.data.lang.menus[2]}</h1>
            </div>
          </El>
          <El>
            <div className="content">
              <div className="row">
                <div className="col-sm-12">
                  <div className="block-title">
                    <h3>{this.props.data.lang.servSubtitle}</h3>
                  </div>
                </div>
                {ItemEdu}
              </div>
            </div>
          </El>
        </div>
      </Container>
    );
  }
}
