import React, {Component} from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';
import {NavLink} from 'react-router-dom';
import Swal from '../CS';

export default class NavBar extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.collapse = this.collapse.bind(this);
    this.cs = this.cs.bind(this);
    this.ConvertLandAndCollapse = this.ConvertLandAndCollapse.bind(this);

    this.state = {
      isOpen: false,
      swalShow: false,
      menu: this.props.data.lang.menus || ['Home', 'Experiences', 'Services', 'Portfolio', 'Language']
    };
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  }

  collapse() {
    this.setState({
      isOpen: false,
    });
  }

  ConvertLandAndCollapse(lang) {
    this.setState({
      isOpen: false,
    });
    this.props.convertLang(lang);
  }

  cs() {
    this.setState({
      swalShow: !this.state.swalShow,
    });
  }

  componentDidMount() {
    window.addEventListener('scroll', this.changeNav);
  }

  changeNav() {
    const distanceY = window.pageYOffset || document.documentElement.scrollTop;
    const shrinkOn = 40;
    const nav = document.getElementById('navbar');

    distanceY > shrinkOn
      ? nav.classList.add('isScrollDown')
      : nav.classList.remove('isScrollDown');
  }

  render() {
    return (
      <Navbar className="fixed-top" color="light" light expand="md" id="navbar">
        <NavbarBrand id="nameBrand" href="/">
          {this.props.data.name}
        </NavbarBrand>
        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink
                exact
                to={process.env.PUBLIC_URL + '/'}
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}>
                {this.props.data.lang.menus[0]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to={process.env.PUBLIC_URL + '/experiences'}
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}>
                {this.props.data.lang.menus[1]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to={process.env.PUBLIC_URL + '/services'}
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}>
                {this.props.data.lang.menus[2]}
              </NavLink>
            </NavItem>
            <NavItem>
              <button
                className="nav-link"
                onClick={this.cs}
                style={{background: 'transparent'}}>
                {this.props.data.lang.menus[3]}              
              </button>
            </NavItem>

            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret title="Select your language">
              {this.props.data.lang.menus[4]}
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem onClick={() => this.ConvertLandAndCollapse('english')}>English</DropdownItem>
                <DropdownItem onClick={() => this.ConvertLandAndCollapse('bahasa')}>Bahasa Indonesia</DropdownItem>
                <DropdownItem onClick={() => this.ConvertLandAndCollapse('sasak')}>Sasak (Lombok)</DropdownItem>
                <DropdownItem onClick={this.cs}>العربية</DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
        <Swal
          show={this.state.swalShow}
          onConfirm={() => this.setState({swalShow: false})}
        />
      </Navbar>
    );
  }
}