const rootRed = (state = data, action) => {
  switch (action.type) {
    case "convLang":
      state = produce(state, draft => {
        switch (action.lang) {
          case "english":
            draft.lang = data.lang;
            break;
          case "bahasa":
            draft.lang = lang.bahasa;
            break;
          case "sasak":
            draft.lang = lang.sasak;
            break;
          case "arabic":
            draft.lang = lang.arabic;
            break;
          default:
            break;
        }
      });
      break;
    default:
      break;
  }
  return state;
};

export default rootRed;